<template>
  <div class="tabs">
    <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-more"></i> tab选项卡</el-breadcrumb-item>
            </el-breadcrumb>
    </div>
    <div class='container'>
      <el-tabs v-model="message">
        <el-tab-pane :label="`未读消息(${unread.length})`" name="first">
          <template v-if="unread.length > 0">
            <el-table :show-header="false" :data="unread" style="100%">
              <el-table-column>
                <template slot-scope="scope">
                  <span class="message_title">{{scope.row.title}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="date" width="180">
              </el-table-column>
              <el-table-column width="120">
                <template slot-scope="scope">
                  <el-button size="small" @click="handleRead(scope.$index)">
                    标记已读
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="handle_row">
              <el-button type="primary" @click="AllRead">全部标记已读</el-button>
            </div>
          </template>
        </el-tab-pane>
        <el-tab-pane :label="`已读消息(${read.length})`" name="second">
          <template v-if="read.length > 0">
           <el-table :show-header="false" :data="read" style="100%">
              <el-table-column>
                <template slot-scope="scope">
                  <span class="message_title">{{scope.row.title}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="date" width="180">
              </el-table-column>
              <el-table-column width="120">
                <template slot-scope="scope">
                  <el-button size="small" type="danger" @click="handleDel(scope.$index)">
                    删除
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="handle_row">
              <el-button type="danger" @click="AllDel">删除全部</el-button>
            </div>
          </template>
        </el-tab-pane>
        <el-tab-pane :label="`回收站(${recycle.length})`" name="there">
          <template v-if="recycle.length > 0">
          <el-table :show-header="false" :data="recycle" style="100%">
              <el-table-column>
                <template slot-scope="scope">
                  <span class="message_title">{{scope.row.title}}</span>
                </template>
              </el-table-column>
              <el-table-column prop="date" width="180">
              </el-table-column>
              <el-table-column width="120">
                <template slot-scope="scope">
                  <el-button size="small" @click="handleRestore(scope.$index)">
                    还原
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="handle_row">
              <el-button type="danger" @click="DelAll">清空回收站</el-button>
            </div>
          </template>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tabs',
  data () {
    return {
      message: 'first',
      unread: [
        {
          date: '2018-04-19 20:00:00',
          title: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
        },
        {
          date: '2018-04-19 21:00:00',
          title: '今晚12点整发大红包，先到先得',
        }
      ],
      read: [
        {
          date: '2018-04-19 20:00:00',
          title: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
        }
      ],
      recycle: [
        {
          date: '2018-04-19 20:00:00',
          title: '【系统通知】该系统将于今晚凌晨2点到5点进行升级维护',
        }
      ]
    }
  },
  methods: {
    handleRead (index) {
      this.read.push(this.unread[index])
      this.unread.splice(index,1)
    },
    handleDel (index) {
      this.recycle.push(this.read[index])
      this.read.splice(index,1)
    },
    handleRestore (index) {
      this.read.push(this.recycle[index])
      this.recycle.splice(index,1)
    },
    AllRead () {
         for(let i=0; i<this.unread.length;i++){
           this.read.push(this.unread[i])
         }
         this.unread = []
      },
    AllDel () {
      for(let i=0; i<this.read.length;i++){
           this.recycle.push(this.read[i])
      }
      this.read = []
    },
      DelAll () {
        this.recycle = []
    }
  },
  computed: {
    unreadNum () {
      return this.unread.length;
    }
  }
}
</script>

<style scoped>
 .message_title {
   cursor: pointer;
   color: #20a0ff;
 }
 .handle_row {
   margin-top: 50px;
 }
</style>